//headers
.header-peq {
  .texto {
    .categoria {
      font-family: $space;
      font-weight: $regular;
      font-size: 14px;
      letter-spacing: 0.02em;
    }

    h1 {
      font-family: $inter;
      font-weight: $bold;
      font-size: 30px;
      letter-spacing: -0.02em;
      line-height: 100%;
      width: 95%;

      @media (min-width: 768px) {
        font-size: 42px;
      }
    }

    .linha-fina {
      font-family: $inter;
      font-weight: $regular;
      font-size: 16px;
      line-height: 125%;
      width: 95%;

      @media (min-width: 768px) {
        font-size: 20px;
      }
    }
  }
}

.header-mid {
  &__int {
    min-height: calc(100vh - 88px);

    @media (min-width: 768px) {
      height: calc(100vh - 101px);
    }

    background: var(--bgfundo);
    margin: 0;

    .texto {
      @media (max-width: 768px) {
        padding: 0 30px;
      }


      &__int {
        @media (min-width: 768px) {
          width: 60%;
        }

        .categoria {
          font-family: $space;
          font-weight: $regular;
          font-size: 14px;
          color: #fff;
          letter-spacing: 0.02em;
        }

        .titulo {
          h1 {
            font-family: $inter;
            font-weight: $bold;
            font-size: 30px;
            letter-spacing: -0.02em;
            line-height: 100%;
            color: #fff;

            @media (min-width: 768px) {
              font-size: 42px;
            }
          }

          .linha-fina {
            font-family: $inter;
            font-weight: $regular;
            font-size: 16px;
            line-height: 120%;
            color: #fff;

            @media (min-width: 768px) {
              font-size: 20px;
              letter-spacing: -0.02em;
            }
          }
        }
      }
    }

    .imagem {
      //height: 360px;

      @media (min-width: 768px) {
        height: calc(100vh - 88px);
      }

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }
  }
}

.header-gde {
  .texto {
    @media (min-width: 768px) {
      width: 75%;
    }

    .categoria {
      font-family: $space;
      font-weight: $regular;
      font-size: 14px;
      letter-spacing: 0.02em;
    }

    h1 {
      font-family: $inter;
      font-weight: $bold;
      font-size: 62px;
      letter-spacing: -0.02em;
      line-height: 100%;
      color: $mural;
    }

    .linha-fina {
      font-family: $inter;
      font-weight: $bold;
      font-size: 30px;
      line-height: 110%;
      letter-spacing: -0.02em;

      @media (min-width: 768px) {
        font-size: 32px;
      }
    }
  }
}

.cortina-efeito {
  top: 0;
  left: 0;
  margin: 0 auto;
  width: 100%;

  &.single-hero {
    left: 0px;
    margin-bottom: 0px;
    position: static;

    &.single-hero-active {
      margin-top: 0px;
      left: 0px;
      position: fixed;
      width: 100%;
      top: 60px;
    }

  }
}


.single {


  &__int {
    .resumo {
      font-family: $space;
      font-weight: $regular;

      @media (min-width: 768px) {
        width: 75%;
      }

      .titulo {
        font-size: 16px;
        color: $mural;

        &:after, &:before {
          content: '';
          border-bottom: 1px solid $mural;
          width: 82px;
          height: 1px;
          display: block;
          transform: translate(-22px);
        }
      }

      p {
        font-size: 13px;
        line-height: 160%;

        &:after {
          content: '';
          border-bottom: 1px solid $mural;
          width: 22px;
          height: 1px;
          display: block;
          transform: translate(-22px);
          margin-top: 5px;
        }
      }

      .t-leitura {
        font-size: 12px;
        letter-spacing: 0.03em;
        color: rgba(0, 0, 0, 0.7);
      }

      .patrocinada {
        span {
          font-family: $space;
          font-weight: $regular;
          font-size: 12px;
          color: rgba(0,0,0,.7);
        }

        &:before {
          content: '';
          border-bottom: 1px solid $mural;
          width: 22px;
          height: 1px;
          display: block;
          transform: translate(-22px, 5px);
        }
      }
    }
  }
}

