  //conteudo de texto
  &__texto {
    @media (min-width: 768px) {
      width: 75%;
    }

    .wp-block-image {
      img {
        height: auto;
      }

      figcaption {
        max-width: 100%;
        padding-left: 22px;
      }
    }

    h1 {
      font-family: $inter;
      font-weight: $bold;
      font-size: 30px;
      margin-top: 1.5rem;
    }

    h2 {
      font-family: $inter;
      font-weight: $bold;
      font-size: 26px;
      margin-top: 1.5rem;
    }

    h3, h4, h5 {
      font-family: $inter;
      font-weight: $bold;
      font-size: 22px;
      margin-top: 1.5rem;
    }


    img {
      max-width: 100%;
      height: auto;
    }

    p, span {
      font-family: $inter;
      font-weight: $regular;
      font-size: 16px;
      line-height: 150%;
      //margin-bottom: 30px;
      margin-bottom: 10px;
      clear: both;

      a {
        font-weight: 600;
        text-decoration: underline;
        text-decoration-color: $mural;
        text-underline-offset: 2px;
      }
    }

    iframe {
      margin-top: 3rem!important;
      margin-bottom: 3rem!important;
      width: 100%;
    }

    .legenda-credito {
      &:after {
        content: '';
        border-bottom: 1px solid #ff5900;
        width: 22px;
        height: 1px;
        display: block;
        transform: translate(-22px, 5px);
      }
    }
  }
}

.after {
  &__tags {
    a {
      font-family: $space;
      font-weight: $regular;
      color: $mural;
      font-size: 11px;
      letter-spacing: 0.05em;

      &:last-child {
        &:after {
          display: none;
        }
      }

      &:after {
        content: '';
        background: $mural;
        width: 1px;
        height: 12px;
        display: inline-block;
        margin: 0 15px;
        transform: translate(0, 2px);
      }
    }
  }

  &__comentarios {
    .open-comentarios {
      font-family: $space;
      font-weight: $regular;
      font-size: 11px;
      letter-spacing: 0.05em;
      background-image: url("data:image/svg+xml,%3Csvg width='18' height='14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.208 13.544a.522.522 0 01-.808-.085l-1.894-2.914A1 1 0 014.345 9h2.874a1 1 0 01.853 1.521l-1.788 2.926a.524.524 0 01-.076.097z' fill='%23000'/%3E%3Crect width='18' height='10.8' rx='2' fill='%23000'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-position: left;
      background-position-x: 12px;
      padding: 8px 13px 7px 40px;
    }
  }

  &__assinaturas {
    .assinaturas-bloco {
      .imagem {
        flex: 0 0 110px;
      }

      .texto {
        span {
          font-family: $space;
          font-weight: $bold;
          font-size: 16px;
          letter-spacing: -0.02em;
        }

        p {
          font-family: $space;
          font-weight: $regular;
          font-size: 12px;
          line-height: 130%;

          a {
            color: $mural;
          }
        }

        .bairro {
          font-size: 10px;
