          width: 100%;
          object-fit: cover;

          @media (min-width: 768px) {
            height: 390px;
          }
        }

        .texto {
          width: 75%;

          @media (min-width: 768px) {
            width: 65%;
          }

          .categoria {
            font-family: $space;
            font-weight: $regular;
            font-size: 13px;
          }

          h2 {
            font-family: $inter;
            font-weight: $bold;
            font-size: 16px;

            @media (min-width: 768px) {
              font-size: 20px;
              letter-spacing: -0.03em;
            }
          }

          .detalhes {
            font-family: $space;
            font-weight: $regular;
            font-size: 12px;
            color: #545454;

            a {
              color: #545454;
            }
          }
        }
      }
    }

    .outras-controls {
      span {
        display: block;
        width: 16px;
        height: 25px;
        mask: url("data:image/svg+xml,%3Csvg width='16' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14 2L3 12.667 14 23' stroke='%23000' stroke-width='3'/%3E%3C/svg%3E");
        -webkit-mask: url("data:image/svg+xml,%3Csvg width='16' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14 2L3 12.667 14 23' stroke='%23000' stroke-width='3'/%3E%3C/svg%3E");
        background-color: #000;
        cursor: pointer;

        position: absolute;
        bottom: 40px;
      }

      .prev {
        left: 10px;

        @media (min-width: 768px) {
          left: 40px;
        }
      }

      .next {
        transform: rotate(180deg);
        right: 10px;

        @media (min-width: 768px) {
          right: 40px;
        }
      }
    }
  }

  .accordion-single {
    span.titulo {
      font-family: $space;
      font-weight: $bold;
      font-size: 24px;
      letter-spacing: 0.05em;
    }

    li {
      &.c-1 {
        background: #F15A24;
      }

      &.c-2 {
        background: #EF8132;
      }

      &.c-3 {
        background: #EBA871;
      }

      &.c-4 {
        background: #D9763D;
      }
    }

    .section-content{
      .bloco {
        &__imagem {
          img {
            height: 200px;
            width: 100%;
            object-fit: cover;
          }
        }

        &__texto {
          font-family: $inter;
          color: #FFFFFF;

          h3 {
            font-weight: $bold;
            font-size: 24px;
            letter-spacing: -0.02em;
          }

          .linha-fina {
            font-weight: $regular;
            font-size: 14px;
            line-height: 140%;
          }
        }
      }
    }
  }
}

.conteudo__texto > p:first-of-type:first-letter {
  display: inline-table;
  float: left;
  margin: -0.1em 0.1em -0.25em 0;
  font-size: 90px;
  line-height: 1;
  font-weight: $bold;
}

//pagina de muralistasl115
.muralistas {
  &__header {
    h1 {
      font-family: $space;
      font-weight: $bold;
      font-size: 26px;
      padding-left: 22px;

      &:after {
        content: '';
        border-bottom: 1px solid #ff5900;
        width: 22px;
        height: 1px;
        display: block;
        transform: translate(-22px, 5px);
      }
    }

    .linha-fina {
      font-family: $space;
      font-weight: $regular;
      font-size: 18px;
      padding-left: 22px;

      &:after {
        content: '';
        border-bottom: 1px solid #ff5900;
        width: 22px;
        height: 1px;
        display: block;
        transform: translate(-22px, 5px);
      }
    }

    .bloco-ordenacao {
      flex: 0 0 270px;
      font-family: $space;
      font-size: 14px;
      font-weight: $regular;
      height: 30px;

      .titulo {
        font-weight: $bold;
        cursor: pointer;

        i {
          background-image: url("data:image/svg+xml,%3Csvg width='14' height='8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1l6.43 5.482L13.056 1' stroke='%23000' stroke-width='2'/%3E%3C/svg%3E");
          background-repeat: no-repeat;
          width: 14px;
          height: 9px;
        }
      }

      .ordenacao-int {
        position: absolute;
        z-index: 0;
        background: #fff;
        top: 30px;
        opacity: 0;

        &.active {
          z-index: 9;
          opacity: 1;
        }
      }
    }
  }

  &__listagem {
    font-family: $space;

    .titulo {
      font-weight: $bold;
      font-size: 24px;
      color: $mural;
    }

    .gestores {
      .imagem {
        background: url("assets/images/bg-azul.png");
      }
    }

    .redacao {
      .imagem {
        background: url("assets/images/bg-vermelho.png");
      }
    }

    .muralistas {
      .imagem {
        background: url("assets/images/bg-amarelo.png");
      }
    }

    .user {
      margin-bottom: 80px;

      .imagem {
        display: block;
        width: 100%;
        height: 268px;
        background-size: cover;

        @media (min-width: 768px) {
          height: 198px;
        }

        img {
          width: calc(100% - 20px);
          height: 223px;
          object-fit: cover;
          margin: auto;
          margin-left: 10px;
          margin-top: 31px;


          @media (min-width: 768px) {
            width: calc(100% - 15px);
            height: 165px;
            object-fit: cover;
            margin: auto;
            margin-left: 8px;
            margin-top: 23px;
          }
        }
      }

      .texto {
        .nome {
          font-weight: $bold;
          font-size: 24px;
          letter-spacing: -0.02em;
          line-height: 110%;

          &:after {
            content: '';
            border-bottom: 1px solid #ff5900;
            width: 12px;
            height: 1px;
            display: block;
            transform: translate(-6px,5px);
          }
        }

        .cargo {
          font-weight: $regular;
          font-size: 14px;
          line-height: 130%;
          letter-spacing: 0.03em;

          &:after {
            content: '';
            border-bottom: 1px solid #ff5900;
            width: 12px;
            height: 1px;
            display: block;
            transform: translate(-6px,5px);
          }
        }

        .since {
          font-weight: $bold;
          color: $mural;
          font-size: 10px;
        }
      }

      .user-zona {
        .zona {
          background: #000;
          color: #fff;
          font-family: $space;
          font-weight: $bold;
          font-size: 13px;
        }

        .bairro {
          font-family: $space;
          font-weight: $bold;
          font-size: 13px;
          border-color: #000 !important;
          background: #fff;
          
          &:hover {
            background: #000 !important;
            color: #fff;
          }
        }
      }
    }
  }
}

